<template>
    <v-container fluid class="full-width">
      <v-toolbar color="transparent" class="elevation-0">
        <!-- <v-select :items="filters" v-model="filter" placeholder="Filter" color="accent"></v-select> -->
         <v-menu
        ref="start_menu"
        color="accent"
        v-model="start_menu"
        :close-on-content-click="false"
        :nudge-right="40"
        :return-value.sync="start"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <v-text-field
          slot="activator"
          v-model="start"
          label="From"
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker :max="new Date().toDateString()" v-model="start" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="accent" @click="start_menu = false">Cancel</v-btn>
          <v-btn flat color="accent" @click="$refs.start_menu.save(start)">OK</v-btn>
        </v-date-picker>
      </v-menu>
        <v-menu
        ref="end_menu"
        color="accent"
        v-model="end_menu"
        :close-on-content-click="false"
        :nudge-right="40"
        :return-value.sync="end"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <v-text-field
          slot="activator"
          v-model="end"
          label="To"
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="end" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="accent" @click="end_menu = false">Cancel</v-btn>
          <v-btn flat color="accent" @click="$refs.end_menu.save(end)">OK</v-btn>
        </v-date-picker>
      </v-menu>
        <v-spacer></v-spacer>
        <v-text-field @keyup="match()" v-model="search" color="accent" prepend-icon="mdi-magnify" placeholder="search">

        </v-text-field>
      </v-toolbar>
        <v-layout row justify-start align-start wrap>
            
            <v-flex  style="padding:10px" v-for="payment in matched" xs12 :key="payment._id">
                <v-list v-if="withinRange(payment)" three-line>
                    <v-list-tile >
                    <v-list-tile-avatar color="accent">
                      <v-btn @click="payAgain(payment.vendor._id, payment.amount, payment.narration)" icon>
                            <v-icon color="white">mdi-redo-variant</v-icon>
                      </v-btn>

                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title class="title white--text" >{{"     "+ payment.vendor.vendor_name}}</v-list-tile-title>
                        <v-list-tile-sub-title color="white--text">{{payment.narration}}</v-list-tile-sub-title>
                        <v-list-tile-sub-title class="subheading white--text"><v-icon >mdi-currency-ngn</v-icon>{{formatMoney(payment.amount)}}</v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                        <v-list-tile-action-text>
                            {{payment.createdOn.slice(0,10)}}
                        </v-list-tile-action-text>
                        <v-list-tile-action-text>
                          {{payment.signatory.name}}
                        </v-list-tile-action-text>
                        <v-icon v-if="payment.status=='success'" color="green">mdi-check-circle</v-icon>
                         <v-icon v-else-if="payment.status.toLowerCase()=='pending'" color="amber">mdi-radiobox-marked</v-icon>
                          <v-icon v-else color="red">mdi-close</v-icon>
                    </v-list-tile-action>
                </v-list-tile>
                </v-list>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
export default {
  props: {
    payments: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      search: null,
      matched: [],
      ascending: false,
      start_menu: false,
      start: null,
      end: null,
      end_menu: false,
      filter: null,
      filters: [
        {
          text: "Today",
          value: 1
        },
        {
          text: "Last 7 days",
          value: 7
        },
        {
          text: "Last 30 days",
          value: 30
        },
        {
          text: "All",
          value: null
        }
      ]
    };
  },
  watch: {
    start(newValue, oldValue) {
      this.match()
    },
    end(newValue, oldValue) {
      this.match()
    },
    search(newValue, oldValue) {
      this.match()
    }
  },
  computed: {
    reversedPayments() {
      return this.payments.reverse();
    }
  },
  mounted() {
    this.matched = JSON.parse(JSON.stringify(this.reversedPayments));
  },

  methods: {
    withinRange(payment) {
      if (this.filter == null || this.filter.value) {
        return true;
      }
      let days = Date.parse(payment.createdOn) / (1000 * 60 * 60 * 24);
      let today = Date.now() / (1000 * 60 * 60 * 24);
      let diff = today - days;
      console.log(diff);
      if (this.filter >= diff) {
        return true;
      } else {
        return false;
      }
    },
    match() {
      this.matched = this.reversedPayments
      if (this.search !== null) {
        this.matched = [];
        let $search = this.search.toLowerCase();
        this.payments.forEach(payment => {
          if (
            payment.vendor.name.toLowerCase().includes($search) ||
            payment.narration.toLowerCase().includes($search) ||
            payment.signatory.name.toLowerCase().includes($search)
          ) {
            this.matched.push(payment);
          }
        });
      }

      let $matched = [];

      this.matched.forEach(payment => {
        let from = this.start ? this.start : "1970-01-01";
        let end = this.end ? this.end : this.formatDate(new Date().toDateString());
        let $date = Date.parse(payment.createdOn)
        from = Date.parse(from)
        end = Date.parse(end)
        if($date <= (end + 24*60*60*1000) && $date >= from){
          $matched.push(payment)
        }

        });

      this.matched = $matched;
    },
    payAgain(who, how_much, why) {
      this.$router.push({ name: "pay", params: { who, why, how_much } });
    },
    formatDate(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    formatMoney(amount, decimalCount = 2, decimal = ".", thousands = ",") {
      try {
        decimalCount = Math.abs(decimalCount);
        decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

        const negativeSign = amount < 0 ? "-" : "";

        let i = parseInt(
          (amount = Math.abs(Number(amount) || 0).toFixed(decimalCount))
        ).toString();
        let j = i.length > 3 ? i.length % 3 : 0;

        return (
          negativeSign +
          (j ? i.substr(0, j) + thousands : "") +
          i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) +
          (decimalCount
            ? decimal +
              Math.abs(amount - i)
                .toFixed(decimalCount)
                .slice(2)
            : "")
        );
      } catch (e) {
        console.log(e);
      }
    }
  }
};
</script>
