<template>
    <v-slide-x-transition>
        <v-container fluid>
            <v-dialog persistent v-model="showDialog" width="500">
              <v-tooltip slot="activator" top>
                 <v-btn
               slot="activator" color="pink lighten-1 white--text"
              dark
              small
              fixed
              bottom
              right
              large
              fab
            >
              <v-icon>add</v-icon>
            </v-btn>
              <span>Add a vendor</span>
            </v-tooltip>
            <v-card>
                <v-toolbar color="accent">
                  <v-toolbar-title>ADD NEW VENDOR</v-toolbar-title>
                  <v-spacer>
                    
                  </v-spacer>
                  <v-btn @click="closeDialog()" icon><v-icon>mdi-close</v-icon></v-btn>
                </v-toolbar>
              <v-card-text>
                <NewVendor  ref="new_vendor" />
              </v-card-text>
              <v-card-actions>
                <v-spacer>
               
                </v-spacer>   <v-btn @click="retrieve()" :loading="isLoading" flat="" color="accent">
                    SUBMIT
                  </v-btn>
              </v-card-actions>
            </v-card>
            
            </v-dialog>
            <v-dialog persistent v-model="editDialog" width="500">
              
            <v-card>
                <v-toolbar color="accent">
                  <v-toolbar-title>EDIT VENDOR</v-toolbar-title>
                  <v-spacer>
                    
                  </v-spacer>
                  <v-btn @click="editDialog = false" icon><v-icon>mdi-close</v-icon></v-btn>
                </v-toolbar>
              <v-card-text>
                <EditVendor :preset="current" ref="edit_vendor" />
              </v-card-text>
              <v-card-actions>
                <v-spacer>
               
                </v-spacer>   <v-btn @click="submitEdit()" :loading="isSaving" flat="" color="accent">
                    SUBMIT
                  </v-btn>
              </v-card-actions>
            </v-card>
            
            </v-dialog>
            
             <v-layout row wrap>
                <v-flex style="padding:10px;" xs12 v-for="item in vendors" :key="item.vendor_name">
             <v-list  three-line>
            <v-list-tile          
              
              avatar
              style="margin-top:10px;"
            >
              <v-list-tile-avatar>
                <v-icon>mdi-account-box</v-icon>
              </v-list-tile-avatar>
          
              <v-list-tile-content>
                <v-list-tile-title v-html="item.vendor_name"></v-list-tile-title>
                <v-list-tile-sub-title class="overflow-hidden caption">{{item.description}}</v-list-tile-sub-title>
                <v-list-tile-sub-title > <v-icon small>mdi-bank</v-icon><strong>{{' '+item.bank_name+' - '+encode(item.account_number)}}</strong></v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-action>
                 <v-menu offset-y>
                      <v-btn
                        slot="activator"
                        color="white--text"
                        icon
                      >
                        <v-icon>mdi-dots-vertical</v-icon>
                      </v-btn>
                      <v-list>
                        <v-list-tile
                          v-for="(_item, index) in actions"
                          :key="index"
                          @click="_item.action(item._id)"
                        >
                        <v-list-tile-avatar>
                          <v-icon small>{{_item.icon}}</v-icon>
                        </v-list-tile-avatar>
                          <v-list-tile-title>{{ _item.title }}</v-list-tile-title>
                        </v-list-tile>
                        
                      </v-list>
                    </v-menu>
              </v-list-tile-action>
            </v-list-tile>

        </v-list>
          </v-flex>
             </v-layout>
         
           <v-snackbar  color="pink lighten-1" 
      v-model="showSnack"
    >
      {{ snackText }}
      <v-btn
        dark
        flat
        left
        @click="showSnack = false"
      >
        Close
      </v-btn>
    </v-snackbar>
        </v-container>
    </v-slide-x-transition>
</template>
<script>
import NewVendor from "./_vendors/new";
import EditVendor from "./_vendors/edit";
import axios from "axios";
export default {
  props: {
    vendors: {
      type: Array,
      required:true 
    },
  },
  components: {
    NewVendor,
    EditVendor
  },
  methods: {
    encode(str){
      if(!str){
        return
      }
      return str.slice(0,3)+ '******'+ str.slice(str.length-3)
    },
    removeVend(id){
      alert("remove")
    },
    editVend(id){
      // alert("edit")
      this.vendors.forEach(element => {
        if(element._id == id){
          this.current = element
        }
      });
      this.editDialog = true
    },
    blacklist(id){
      alert("Blacklist")
    },
    closeDialog(){
      this.showDialog=false
    },
    submitEdit: function() {
      let vendor = this.$refs.edit_vendor.collect();
      if (!vendor || !vendor.account_name) {
        return;
      }
      try {
        vendor.bank_code = vendor.bank.value;
        vendor.bank_name = vendor.bank.text;
        delete vendor.bank;
      } catch (e) {}
      this.isSaving = true;
      axios
        .patch("https://bakerrpay.herokuapp.com/api/vendors/"+vendor._id, vendor, {withCredentials:true})
        .then(resp => {
          this.isSaving = false;
          console.log(resp);
          if(resp.data.state=="OK"){
            this.editDialog = false;
            this.$refs.edit_vendor.$refs._form.reset();
            this.snackText="Vendor edited successfully"
            this.$emit('reload')  
            this.showSnack = true;
          }else{
            this.showDialog = false;
            this.$refs.edit_vendor.$refs._form.reset();
            this.showSnack = true
            this.snackText = resp.data.state
          }
        })
        .catch(err => {
          this.showSnack = true;
          this.snackText  = "Oooops! An error occured, try again"
          this.isSaving = false;
          console.log(err);
        });
    },
    retrieve: function() {
      let vendor = this.$refs.new_vendor.collect();
      if (!vendor || !vendor.account_name) {
        return;
      }
      try {
        vendor.bank_code = vendor.bank.value;
        vendor.bank_name = vendor.bank.text;
        delete vendor.bank;
      } catch (e) {}
      this.isLoading = true;
      axios
        .post("https://bakerrpay.herokuapp.com/api/vendors", vendor, {withCredentials:true})
        .then(resp => {
          this.isLoading = false;
          console.log(resp);
          if(resp.data.state=="OK"){
            this.showDialog = false;
            this.$refs.new_vendor.$refs._form.reset();
            this.snackText="New vendor added successfully"
            this.$emit('reload')  
            this.showSnack = true;
          }else{
            this.showDialog = false;
            this.$refs.new_vendor.$refs._form.reset();
            this.showSnack = true
            this.snackText = resp.data.state
          }
        })
        .catch(err => {
          this.showSnack = true;
          this.snackText  = "Oooops! An error occured, try again"
          this.isLoading = false;
          console.log(err);
        });
    }
  },
  data() {
    return {
      showDialog: false,
      isLoading: false,
      snackText:null,
      showSnack:false,
      current:{},
      editDialog: false,
      isSaving: false,
      actions:[
        {
          title: "Remove",
          icon: "mdi-delete",
          action: this.removeVend
        },
        {
          title: "Edit",
          icon: "mdi-pen",
          action: this.editVend
        },
        {
          title: "Blacklist",
          icon: "mdi-block-helper",
          action: this.blacklist
        }
      ]
    };
  }
};
</script>
